.dialogs {
    display: grid;
    grid-template-columns: 2fr 10fr;
    margin-left: 10px;
}

.dialogsItems {
    padding: 20px;
}

.dialogsItems .active {
    color: gold;
}

.messages  {
    margin: 5px 50px;
    padding: 28px 4px;
    text-align: left;
}

.messages .message {
}

.item img {
    display: block;
    margin-left: auto;
    margin-right: auto;
    width: 50px;
    height: 50px;
    border-radius: 80%;
    margin-top: 15px;
}

.name {
    text-align: center;
    text-decoration: none;
}

.navLink {
    text-decoration: none
}
